package me.ksviety.room.client.components.screens

import kotlinx.css.*
import me.ksviety.room.api.models.Room
import me.ksviety.room.client.components.chat.Chat
import me.ksviety.room.client.screen
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import styled.css
import styled.styledDiv
import styled.styledImg
import styled.styledSection

class RoomScreen : RComponent<RoomScreen.Props, RoomScreen.State>() {

    interface Props : RProps {
        var onLeave: () -> Unit
        var room: Room
    }

    interface State : RState

    override fun RBuilder.render() {
        screen {
            styledDiv {
                css {
                    width = 100.pct
                    height = 100.pct
                    display = Display.flex
                    flexDirection = FlexDirection.rowReverse
                    position = Position.relative

                    media("screen and (orientation: portrait)") {
                        flexDirection = FlexDirection.columnReverse
                    }
                }
                styledSection {
                    css {
                        width = 350.px
                        height = 100.pct
                        position = Position.relative

                        media("screen and (orientation: portrait)") {
                            width = 100.pct
                        }
                    }

                    child(Chat::class) {
                        attrs.room = props.room
                    }
                }
                /*
                styledSection {
                    css {
                        width = 100.pct
                        position = Position.relative
                        backgroundColor = Color.blue
                        height = 100.pct

                        zIndex = 200

                        media("screen and (orientation: portrait)") {
                            height = LinearDimension.auto
                        }
                    }
                    styledImg {
                        attrs.src = "https://via.placeholder.com/1920x1080.png?text=Video+Player"
                        css { position = Position.relative; width = 100.pct; height = LinearDimension.auto }
                    }
                }
                */
            }
        }
    }
}