package me.ksviety.room.client.components.forms

import kotlinx.css.*
import kotlinx.css.properties.boxShadow
import kotlinx.html.js.onChangeFunction
import kotlinx.html.js.onSubmitFunction
import me.ksviety.room.client.button
import org.w3c.dom.HTMLInputElement
import org.w3c.dom.events.Event
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import styled.*

class ChatInputForm : RComponent<ChatInputForm.Props, ChatInputForm.State>() {

    init {
        state.message = String()
    }

    interface Props : RProps {
        var onSend: (String) -> Unit
    }

    interface State : RState {
        var message: String
    }

    private fun handleInputChange(event: Event) {
        val target = event.target as? HTMLInputElement ?: return
        setState(state.apply { message = target.value })
    }

    private fun submit() {
        val message = if (state.message.isNotBlank()) state.message else return
        props.onSend(message)
        setState(state.apply { this.message = String() })
    }

    override fun RBuilder.render() {
        styledForm {
            attrs.onSubmitFunction = { it.preventDefault(); submit() }
            css {
                padding(10.px)
                display = Display.flex
                justifyContent = JustifyContent.baseline
                boxShadow(Color("#0004"), 0.px, (-2).px, 10.px, 0.px)
                zIndex = 150
            }
            styledInput {
                css {
                    width = 100.pct
                    display = Display.block
                    backgroundColor = Color.transparent
                    border = "none"
                    padding(5.px, 5.px)
                    color = Color.inherit
                    fontSize = 0.8.em

                    outline = Outline.none
                }
                attrs {
                    placeholder = "Write a message..."
                    autoFocus = true
                    value = state.message
                    onChangeFunction = { it.preventDefault(); handleInputChange(it) }
                }
            }
            button("Send")
        }
    }
}