package me.ksviety.room.client.components.chat

import kotlinx.coroutines.MainScope
import kotlinx.coroutines.launch
import kotlinx.css.*
import kotlinx.css.properties.boxShadow
import me.ksviety.room.api.models.ChatMessage
import me.ksviety.room.api.models.Room
import me.ksviety.room.api.models.User
import me.ksviety.room.api.sendMessage
import me.ksviety.room.client.components.forms.ChatInputForm
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import styled.css
import styled.styledDiv
import kotlin.js.Date

class Chat : RComponent<Chat.Props, Chat.State>() {

    init {
        state.messages = arrayListOf()
    }

    interface Props : RProps {
        var room: Room
    }

    interface State : RState {
        var messages: ArrayList<ChatMessage>
    }

    private fun handleSend(text: String) {
        val temp = ChatMessage(-state.messages.size, text, props.room.self, Date())
        var index = 0

        setState(state.apply {
            index = messages.size
            messages.add(temp)
        })

        MainScope().launch {
            val message = sendMessage(text)
            setState(state.apply {
                messages[index] = message
            })
        }
    }

    override fun RBuilder.render() {
        styledDiv {
            css {
                display = Display.flex
                position = Position.relative
                flexDirection = FlexDirection.column

                overflow = Overflow.hidden
                borderLeft = "solid 1px black"
                boxShadow(Color("#0004"), 0.px, 0.px, 15.px, 0.px)
                height = 100.pct
            }
            child(ChatMessagesList::class) {
                attrs.messages = state.messages
                attrs.room = props.room
            }
            child(ChatInputForm::class) {
                attrs.onSend = { handleSend(it) }
            }
        }
    }
}