package me.ksviety.room.client.components.forms

import kotlinx.css.*
import kotlinx.css.properties.*
import kotlinx.html.INPUT
import kotlinx.html.js.onChangeFunction
import kotlinx.html.js.onSubmitFunction
import me.ksviety.room.client.button
import org.w3c.dom.HTMLInputElement
import org.w3c.dom.events.Event
import org.w3c.dom.url.URLSearchParams
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import styled.*
import kotlin.browser.window

class MainScreenForm : RComponent<MainScreenForm.Props, MainScreenForm.State>() {

    private val fieldHeight = 30

    init {
        state.token = URLSearchParams(window.location.search).get("token") ?: ""
        state.username = ""

        if (state.token.isNotBlank())
            state.function = Function.Join
        else
            state.function = Function.Create
    }

    override fun componentDidUpdate(prevProps: Props, prevState: State, snapshot: Any) {
        console.log("Updated")
    }

    enum class Function {
        Join, Create
    }

    interface Props : RProps {
        var onJoin: (username: String, token: String) -> Unit
        var onCreate: (username: String) -> Unit
    }

    interface State : RState {
        var username: String
        var token: String
        var function: Function
    }

    private fun handleUsernameFieldChange(event: Event) {
        val input = event.target as? HTMLInputElement

        event.preventDefault()

        input?.let {
            setState(state.apply { username = it.value })
        }
    }

    private fun handleTokenFieldChange(event: Event) {
        val input = event.target as? HTMLInputElement

        event.preventDefault()

        input?.let {
            setState(state.apply { token = it.value })
        }
    }

    private fun submit() {
        if (state.username.isBlank()) return
        if (state.function == Function.Join && state.token.isBlank()) return

        when (state.function) {
            Function.Join -> props.onJoin(state.username, state.token)
            Function.Create -> props.onCreate(state.username)
        }
    }

    private fun RBuilder.joinButton() = button {
        text = "Join"
        tint = state.function == Function.Join
        trailing = if (state.function == Function.Create) 10.px else null
        onClick = {
            setState(state.apply {
                function = Function.Join
            })
        }
    }

    private fun RBuilder.createButton() = button {
        text = "Create"
        tint = state.function == Function.Create
        trailing = if (state.function == Function.Join) 10.px else null
        onClick = {
            setState(state.apply {
                function = Function.Create
            })
        }
    }

    private fun RBuilder.field(handler: INPUT.() -> Unit) = styledInput {
        css {
            display = Display.block
            border = "none"
            width = 100.pct
            padding(5.px)
            textAlign = TextAlign.center
            backgroundColor = Color.transparent
            height = fieldHeight.px
        }
        attrs(handler)
    }

    override fun RBuilder.render() {
        styledForm { /* Main form container */
            attrs.onSubmitFunction = { it.preventDefault(); submit() }
            styledDiv { /* Fields container */
                css {
                    backgroundColor = Color("#EEE")
                    overflow = Overflow.hidden
                    border(width = 1.px, color = Color.lightGray, style = BorderStyle.solid, borderRadius = 7.px)

                    height = when (state.function) {
                        Function.Join -> (fieldHeight * 2 + 1 ).px
                        Function.Create -> fieldHeight.px
                    }

                    if (state.function == Function.Join) {
                        animation(duration = 0.2.s, timing = Timing.ease, playState = PlayState.running, fillMode = FillMode.forwards) {
                            from { height = fieldHeight.px }
                            to { height = (fieldHeight * 2 + 1 ).px }
                        }
                    } else if (state.function == Function.Create) {
                        animation(duration = 0.2.s, timing = Timing.ease, playState = PlayState.running, fillMode = FillMode.forwards) {
                            from { height = (fieldHeight * 2 + 1 ).px }
                            to { height = fieldHeight.px }
                        }
                    }
                }

                field {
                    placeholder = "Username"
                    name = "username"
                    autoFocus = true
                    maxLength = "30"
                    required = true
                    value = state.username
                    onChangeFunction = { handleUsernameFieldChange(it) }
                }
                styledDiv { css { height = 1.px; backgroundColor = Color.lightGray} }
                field {
                    placeholder = "Token"
                    name = "token"
                    autoComplete = false
                    maxLength = "16"
                    minLength = "16"
                    required = state.function == Function.Join
                    disabled = state.function == Function.Create
                    value = state.token
                    onChangeFunction = { handleTokenFieldChange(it) }
                }
            }
            styledDiv {  /* Buttons container */
                css {
                    display = Display.flex
                    flexDirection = FlexDirection.rowReverse
                    alignContent = Align.baseline
                    marginTop = 15.px

                }
                when (state.function) {
                    Function.Join -> {
                        joinButton()
                        createButton()
                    }
                    Function.Create -> {
                        createButton()
                        joinButton()
                    }
                }
            }
        }
    }
}