package me.ksviety.room.client.components.chat

import kotlinx.css.*
import kotlinx.css.properties.*
import me.ksviety.room.api.models.ChatMessage
import me.ksviety.room.api.models.Room
import me.ksviety.room.client.TINT_COLOR
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.dom.div
import react.dom.li
import styled.*
import kotlin.browser.document
import kotlin.browser.window
import kotlin.js.Date

class ChatMessagesList : RComponent<ChatMessagesList.Props, ChatMessagesList.State>() {

    interface Props : RProps {
        var messages: List<ChatMessage>
        var room: Room
    }

    interface State : RState

    private fun RBuilder.message(message: ChatMessage) = styledDiv {
        key = message.id.toString()
        css {
            padding(7.px, 0.px)
            display = Display.flex
            flexDirection = if (message.sender == props.room.self) FlexDirection.rowReverse else FlexDirection.row
        }
        styledSpan { /* Time */
            css {
                fontSize = 0.7.em
                color = Color.lightGray
                verticalAlign = VerticalAlign.baseline
                padding(0.px, 7.px)
            }
            + message.time.toLocaleTimeString(window.navigator.language, dateLocaleOptions { hour = "2-digit"; minute = "2-digit" })
        }
        styledSpan { /* Main Container */
            css {
                padding(5.px, 7.px)
                backgroundColor = Color.transparent
                color = Color.inherit
                border(1.px, BorderStyle.dashed, Color(TINT_COLOR), 5.px)
                fontSize = 0.75.em
                display = Display.flex
                flexDirection = FlexDirection.column
                verticalAlign = VerticalAlign.baseline
                maxWidth = 60.pct

                if (message.id >= 0) {
                    animation(duration = 0.3.s, fillMode = FillMode.forwards, playState = PlayState.running) {
                        from { backgroundColor = Color.transparent }
                        to { backgroundColor = Color(TINT_COLOR) }
                    }
                }
            }
            styledDiv { /* Header Container */
                css {
                    display = Display.flex
                    justifyContent = JustifyContent.spaceBetween
                    alignItems = Align.center
                }
                if (message.sender != props.room.self) {
                    styledSpan { /* Sender Username */
                        css {
                            color = Color.inherit
                        }
                        + message.sender.username
                    }
                }
            }
            styledSpan {
                css {
                    overflowWrap = OverflowWrap.breakWord
                    fontSize = 1.2.em
                }
                +message.text
            }
        }
    }

    override fun RBuilder.render() {
        styledDiv {
            css {
                position = Position.relative
                width = 100.pct
                height = 100.pct
                display = Display.flex
                alignItems = Align.flexEnd
                padding(10.px, 5.px)

                overflowY = Overflow.hidden
            }
            styledDiv {
                css {
                    width = 100.pct
                    display = Display.flex
                    flexDirection = FlexDirection.column
                    position = Position.relative
                }
                props.messages.map { message(it) }
            }
        }
    }
}