package me.ksviety.room.client.components

import kotlinx.css.*
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import styled.css
import styled.styledDiv

class Screen : RComponent<Screen.Props, Screen.State>() {

    interface Props : RProps

    interface State : RState

    override fun RBuilder.render() {
        styledDiv {
            css {
                fontSize = 1.em
                color = Color("#EEE")
                position = Position.fixed
                width = 100.vw
                height = 100.vh
            }
            children()
        }
    }
}