package me.ksviety.room.client.components.screens

import kotlinx.coroutines.MainScope
import kotlinx.coroutines.launch
import kotlinx.css.*
import kotlinx.css.properties.*
import me.ksviety.room.api.create
import me.ksviety.room.api.join
import me.ksviety.room.api.models.Room
import me.ksviety.room.client.TINT_COLOR
import me.ksviety.room.client.components.forms.MainScreenForm
import me.ksviety.room.client.screen
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.dom.h1
import react.dom.h3
import styled.animation
import styled.css
import styled.styledDiv
import styled.styledSpan

class MainScreen : RComponent<MainScreen.Props, MainScreen.State>() {

    init {
        state.loading = false
    }

    interface Props : RProps {
        var onEnter: (Room) -> Unit
    }

    interface State : RState {
        var loading: Boolean
    }

    private fun handleCreate(username: String) {
        setState(state.apply { loading = true })
        MainScope().launch {
            val (room, user) = create(username)
            props.onEnter(room)
        }
    }

    private fun handleJoin(username: String, token: String) {
        setState(state.apply { loading = true })
        MainScope().launch {
            val (user, room) = join(username, token)
            props.onEnter(room)
        }
    }

    override fun RBuilder.render() {
        screen {

            styledDiv {
                css {
                    display = Display.flex
                    height = 100.pct
                    justifyContent = JustifyContent.center
                    alignItems = Align.center
                }

                if (state.loading) {

                    styledSpan {
                        css {
                            padding(20.px)
                            border(2.px, BorderStyle.dotted, Color(TINT_COLOR), 666.px)

                            animation(duration = 1.s, timing = Timing.ease, playState = PlayState.running, iterationCount = IterationCount.infinite, direction = AnimationDirection.alternate) {
                                from { padding(20.px) }
                                to { padding(30.px) }
                            }

                        }
                    }

                } else {

                    styledDiv {
                        css {
                            width = 300.px
                            textAlign = TextAlign.center

                            media("screen and (orientation: portrait)") {
                                width = 80.pct
                            }
                        }
                        child(MainScreenForm::class) {
                            attrs.onCreate = { handleCreate(it) }
                            attrs.onJoin = { username, token ->  handleJoin(username, token) }
                        }
                    }

                }

            }

        }
    }
}