package me.ksviety.room.client

import me.ksviety.room.client.components.Button
import me.ksviety.room.client.components.Screen
import react.RBuilder

fun RBuilder.screen(builder: RBuilder.() -> Unit) = child(Screen::class, builder)

fun RBuilder.button(text: String, tint: Boolean = false) = child(Button::class) {
    attrs.text = text
    attrs.tint = tint
}

fun RBuilder.button(handler: Button.Props.() -> Unit) = child(Button::class) { attrs(handler) }