package me.ksviety.room.client.components

import kotlinx.css.*
import kotlinx.css.properties.border
import kotlinx.css.properties.s
import kotlinx.css.properties.transition
import kotlinx.html.js.onClickFunction
import me.ksviety.room.client.ConstraintProps
import me.ksviety.room.client.TINT_COLOR
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import styled.css
import styled.styledButton

class Button : RComponent<Button.Props, Button.State>() {

    interface Props : RProps, ConstraintProps {
        var text: String
        var tint: Boolean
        var onClick: (() -> Unit)?
    }

    interface State : RState

    override fun RBuilder.render() {
        styledButton {
            css {
                val borderColor = if (props.tint) Color.transparent else Color(TINT_COLOR)

                display = Display.block

                padding(4.px, 10.px)
                backgroundColor = if (props.tint) Color("#e31464") else Color.transparent
                color = if (props.tint) Color("#eee") else Color(TINT_COLOR)
                border(1.5.px, BorderStyle.solid, borderColor, 5.px)
                margin(props.top, props.trailing, props.bottom, props.leading)
                transition("filter", duration = 0.1.s)
                active {
                    filter = "brightness(70%)"
                }
            }
            attrs.onClickFunction = { props.onClick?.let { it() } }
            +props.text
        }
    }
}