package me.ksviety.room.client.components

import me.ksviety.room.api.models.Room
import me.ksviety.room.client.components.screens.MainScreen
import me.ksviety.room.client.components.screens.RoomScreen
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState

class App : RComponent<App.Props, App.State>() {

    init {
        state.room = null
    }

    interface Props : RProps

    interface State : RState {
        var room: Room?
    }

    override fun RBuilder.render() {

        state.room?.let { room ->

            child(RoomScreen::class) {
                attrs.room = room
                attrs.onLeave = {
                    setState(state.apply { this.room = null })
                }
            }

        } ?: run {

            child(MainScreen::class) {
                attrs.onEnter = {
                    setState(state.apply {
                        room = it
                    })
                }
            }

        }

    }
}